  a,
  h1,
  #back-top {
    color: #f6f6f6;
    text-decoration: none;
    font-family:Roboto;

  }
  
  .content {
    width: 75%;
    margin: 50px auto
  }
  
  #back-top {
    position: fixed;
    bottom: 0;
    left: 0%;
    z-index: 1000;
    display: none;
    cursor: pointer;
    font-size: 1.6rem;
    padding: 1rem
  }
  
  .fly {
    position: absolute;
    animation: launch ease-in-out 3s infinite;
    -webkit-animation: launch ease-in-out 3s infinite
  }
  
  @-webkit-keyframes launch {
    100% {
      -webkit-transform: translateY(-800px) translateX(800px);
              transform: translateY(-800px) translateX(800px);
    }
  }
  @keyframes launch {
    100% {
      -webkit-transform: translateY(-800px) translateX(800px);
              transform: translateY(-800px) translateX(800px);
    }
  }
  @media (min-width: 500px) {
    .content {
      width: 45%
    }
  }
html,body,#app{height:100vh;margin:0px;padding:0px;width:100vw}body{background-color:#10101a}body h1,body a{color:#5a5a5a;font-family:"Roboto", sans-serif;font-weight:300;margin:0px;padding:0px}body .context-menu-item{background-color:white(255, 255, 255);border-width:2px;border-style:solid;border-color:#040325;border-radius:100px;box-shadow:0px 0px 24px 6px rgba(0,0,0,0.08);height:60px;outline:none;padding:0px;position:absolute;transition:left 0.25s, top 0.25s, opacity 0.25s;width:60px}body .context-menu-item:hover{cursor:pointer;-webkit-transform:scale(1.2);transform:scale(1.2);z-index:2}body .context-menu-item:hover h1{opacity:1;right:-10px}body .context-menu-item:hover i{color:#10101a}body .context-menu-item:active{-webkit-transform:scale(1);transform:scale(1)}body .context-menu-item h1{background-color:#282828;border-radius:4px;color:white;font-size:0.8em;font-weight:700;opacity:0;padding:10px;pointer-events:none;position:absolute;right:0px;text-transform:uppercase;top:50%;-webkit-transform:translate(100%, -50%);transform:translate(100%, -50%);transition:all 0.25s;z-index:2}body .context-menu-item i{font-size:1.5em}body #app #instructions{left:50%;opacity:0.6;pointer-events:none;position:absolute;top:60%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}body #app #instructions h1{color:white;font-size:1em}

